import React from "react";
import { useRef } from "react";
import {
  changeViewType,
  changeValue,
  changeComment,
} from "../../redux-state/reducers/view-type-for-main";
import Footer from "../views/global/Footer";
import css from "../../styles/form.css";
import InputComponent from "../comps/InputComponent";
import FormControl from "@mui/joy/FormControl";
import FormLabel from "@mui/joy/FormLabel";
import Radio from "@mui/joy/Radio";
import RadioGroup from "@mui/joy/RadioGroup";
import { useDispatch, useSelector } from "react-redux";

const { FormContainer, Input, Button, ButtonMenu } = css;

const Main = (props) => {
  const { action } = props;
  const valueInput = useRef();

  const dispatch = useDispatch();
  const viewType = useSelector((state) => state.viewTypeMain.viewType);
  const viewValue = useSelector((state) => state.viewTypeMain.value);
  const viewComment = useSelector((state) => state.viewTypeMain.comment);

  const validation = () => {
    if (viewValue.length > 0) {
      console.log(" ");

      const dataLine = `${viewValue}::${viewType}::${viewComment}`;
      action(dataLine);

      dispatch(changeValue(""));
      dispatch(changeViewType(""));
      dispatch(changeComment(""));
    } else {
      console.log("  ");
    }
  };

  const handleChange = (event) => {
    dispatch(changeViewType(event.target.value));
  };

  const handleChangeValue = (param) => {
    dispatch(changeValue(param));
  };

  const handleChangeComment = (param) => {
    dispatch(changeComment(param));
  };

  const handleChangeCommentRadio = (event) => {
    dispatch(changeComment(event.target.value));
  };

  const setFocus = () => {
    valueInput.current.disabled = false;
    valueInput.current.focus();
  };

  return (
    <React.Fragment>
      <FormContainer>
        <ButtonMenu onClick={setFocus} style={{ marginBottom: "12px" }}>
           
        </ButtonMenu>
        <Input
          ref={valueInput}
          value={viewValue}
          type={"text"}
          placeholder={"  "}
          maxLength={"100"}
          disabled
          onChange={(event) => {
            const newValue = event.target.value;
            handleChangeValue(newValue);
          }}
        />

        {false && (
          <InputComponent
            ref={valueInput}
            inputValue={viewValue}
            action={handleChangeValue}
            placeholder={"  "}
          />
        )}

        {/* <InputComponent
          newValue={viewValue.replace(/\D/g, "")}
          action={handleChangeValue}
          place={"  "}
        /> */}
        <FormControl>
          <FormLabel>  </FormLabel>
          <RadioGroup
            defaultValue="female"
            name="controlled-radio-buttons-group"
            value={viewType}
            onChange={handleChange}
            sx={{ my: 1 }}
          >
            <Radio value="" label="" />
            <Radio value="" label="" />
          </RadioGroup>
        </FormControl>
        {viewType === "" && (
          <InputComponent
            newValue={viewComment}
            action={handleChangeComment}
            place={" "}
          />
        )}
        {viewType === "" && (
          <FormControl>
            <FormLabel>  </FormLabel>
            <RadioGroup
              defaultValue="female"
              name="controlled-radio-buttons-group"
              value={viewComment}
              onChange={handleChangeCommentRadio}
              sx={{ my: 1 }}
            >
              <Radio value=" " label=" " />
              <Radio value=" " label=" " />
              <Radio value=" " label=" " />
              <Radio value="  " label="  " />
              <Radio value="" label="" />
              <Radio value="" label="" />
            </RadioGroup>
          </FormControl>
        )}
        <Button
          onClick={validation}
          backcolor={
            viewValue.length < 3
              ? "rgb(229, 229, 229)"
              : viewType.length < 3
              ? "rgb(229, 229, 229)"
              : "#b0f347"
          }
        >
           
        </Button>
      </FormContainer>
      <Footer></Footer>
    </React.Fragment>
  );
};

export default Main;
